.progress__section {
  // margin-bottom: 50px;
}

.progress__container {
  max-width: 1000px;
  position: relative;
  overflow: hidden;
  padding: 0 5px;
}

.progress__percentage {
  background-image: url(../images/progress-percentage-bg.svg);
  background-repeat: no-repeat;
  background-position: 50% 1px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--primary-color);
  font-size: 24px;
  font-weight: 500;
  padding-top: 5px;
  width: 100%;
  z-index: 2;

  @media screen and (max-width: 439px) {
    position: absolute;
    bottom: -5px;
    left: 0;
    transform: scale(0.8);
  }

  @media screen and (min-width: 440px) and (max-width: 767px) {
    position: absolute;
    bottom: 8px;
    left: 0;
    transform: scale(1.3);
  }
}

.progress__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  // z-index: 3;
  padding: 30px;
  min-height: 100px;
  background-color: rgba(21, 18, 38, 0.7);
  border-radius: 60px;
}

.progress__button-link {
  height: 55px;
  width: 160px;
  min-width: 160px;
  border-radius: 30px;
  background-color: transparent;
  border: 1px solid rgba(99, 97, 113, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  color: hsla(0, 0%, 100%, 0.5);
  font-size: 22px;
  font-weight: 300;
  transition: color 0.35s ease;
  text-align: center;
  cursor: pointer;
  z-index: 3;

  @media screen and (max-width: 439px) {
    height: 50px;
    width: 140px;
    font-size: 18px;
  }

  &:hover,
  &:focus {
    color: var(--primary-color);
    border-color: var(--primary-color);
  }
}

.progress__button-icon {
  width: 27px;
  height: 27px;
  margin-right: 15px;
  fill: currentColor;

  // @media screen and (max-width: 439px) {
  //   width: 24px;
  //   height: 24px;
  // }
}

.progress__button-icon--help {
  margin-left: 15px;
  margin-right: 0;
}

@keyframes lineactive {
  0% {
    transform: translateX(-100%);
  }
  // 50% {
  //   transform: translateX(1);
  // }
  to {
    transform: translateX(0%);
  }
}

.progress-bar__wrap {
  position: relative;
  width: 100%;
  padding-top: 20px;
  padding-left: 40px;
  padding-right: 40px;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  margin-top: auto;
  margin-bottom: auto;

  @media screen and (max-width: 767px) {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
  }
}

.progress-bar__line {
  width: 100%;
  height: 1px;
  position: relative;
  overflow: hidden;
}

.progress-bar__line-back {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  height: 2px;
  width: 100%;
  background-color: #615665;
  z-index: 2;
}

.progress-bar__line-done {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  height: 2px;
  // width: calc((((100% - 20px) / 13) * 3) + 20px);
  background-color: #ceef31;
  z-index: 3;
}

.progress-bar__line-done--calc {
  animation: lineactive 3s ease-in-out 1s both;
}

.progress-bar__dot {
  width: 100%;
  position: relative;
  top: -7px;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  z-index: 4;
}

.progress-bar__circle {
  position: relative;
  width: 14px;
  height: 14px;
  background: #151226;
  border: 2px solid #615665;
  border-radius: 50%;
  cursor: pointer;

  &:hover .progress-bar__dot-desc {
    opacity: 0.5;
  }

  &.done {
    background-color: #ceef31;
    border: 3px solid var(--primary-color);
  }

  &.active {
    border-color: var(--primary-color);
  }

  &.active .progress-bar__dot-desc {
    opacity: 1;
    top: 15px;
  }
}

.progress-bar__dot-desc {
  position: absolute;
  min-width: 100px;
  text-align: center;
  opacity: 0;
  top: -40px;
  transform: translateX(-45%);
  color: var(--primary-color);
  pointer-events: none;
  z-index: 6;
}

.help__modal-wrap {
  width: 100%;
  max-width: 560px;
  border-radius: 46px;
  background-color: hsla(0, 0%, 100%, 0.95);
  box-shadow: 0 20px 40px 0 #201d34;
  max-height: calc(100vh - 30px);
  position: relative;
  padding: 30px 30px 60px;
}

.help__modal-close-wrap {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 58px;
  opacity: 0.9;
  border-radius: 50%;
  overflow: hidden;
  background-image: linear-gradient(90deg, #636171, #4b495c 97%);
  box-shadow: 0 20px 30px 0 rgb(47 43 71 / 60%);
  border: 1px solid #6f6b8d;
  transition: border 0.35s ease;
  position: absolute;
  top: -70px;
  right: 0;

  @media screen and (min-width: 768px) {
    top: -58px;
    right: -58px;
  }

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    background-image: linear-gradient(90deg, #ceef31, #91ef31);
    transition: opacity 0.35s ease;
  }

  &:hover::before {
    opacity: 1;
  }
}

.help__modal-close-icon {
  width: 25px;
  height: 25px;
  fill: var(--primary-color);
  z-index: 1;
}

.help__modal-icon {
  fill: var(--primary-color);
  width: 40px;
  height: 40px;
}

.help__modal-icon-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 70px;
  height: 70px;
  opacity: 0.9;
  border: 1px solid;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 35px;
}

.help__modal-icon-wrap--male {
  background-image: linear-gradient(90deg, #33f5e0, #5172e8);
  border-color: #5bd8f0;
  box-shadow: 0 20px 30px 0 rgb(13 38 102 / 60%);
}

.help__modal-icon-wrap--female {
  background-image: linear-gradient(90deg, #f53373, #bf51e8);
  box-shadow: 0 20px 30px 0 rgb(76 5 45 / 60%);
  border-color: #ef5eb9;
}

.help__modal-desc {
  max-width: 393px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 300;
  text-align: left;
  line-height: 1.3;
  color: #636171;
}
